.main-categories-page-banner {
  width: 100vw;
  height: 100vh;
  height: calc(100vh - 80px);
  padding: 5% 50px;
  display: flex;
  flex-direction: row;
  /* background-image: url("./designing-websites.jpg"); */
  background-size: 100% 100%;
  gap: 2%;
}

.main-categories-banner-content-1 {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* gap: 5%; */
  /* padding-top: 6%;
    background-color: green; */
}

.main-categories-banner-content-1 > div {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 5%;
}

.main-categories-banner-content-1 h1 {
  font-weight: bold;
  color: #fff;
}

.main-categories-banner-content-1 h3 {
  font-weight: bold;
  color: #fff;
}

.main-categories-banner-content-2 {
  flex: 1;
  /* background-color: black; */
}

.main-categories-banner-content-1-buttons {
  display: flex;
  gap: 3%;
}

.main-categories-banner-content-1-buttons button {
  width: fit-content;
  padding: 1.5%;
  background-color: #8e06c2;
  color: #fff;
  font-weight: 600;
  border: none;
}

@keyframes slideInFromBottom {
  0% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(0);
  }
}

.slide-in {
  animation: slideInFromBottom 0.5s ease-in-out;
}
